body {
  font-family: "Barlow Semi Condensed", sans-serif;
  text-transform: uppercase;
}
* {
  margin: 0 !important;
  padding: 0;
  box-sizing: border-box;
}
button {
  cursor: pointer;
}
#root {
  color: hsl(229, 25%, 31%);
  background: linear-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  height: 100vh;
}
/* Header */
#header {
  outline: 3px solid hsl(217, 16%, 45%);
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
}
#header img {
  width: 100px;
}
#score-card {
  border-radius: 5px;
  background: linen;
  padding: 1rem 2rem;
  text-align: center;
  line-height: 1;
}
#score-label {
  color: hsl(229, 64%, 46%);
  font-size: 0.8rem;
}
#score {
  font-weight: 700;
  font-size: 2rem;
  position: relative;
}
#index {
  position: absolute;
  font-size: 1rem;
  line-height: 20px;
}
/* main */
main {
  width: 100%;
  display: flex;
  justify-content: center;
  color: #fff;
}
/* phase set */
#phase_set {
  width: fit-content;
}
#triangle-box {
  position: relative;
  width: 304px;
  height: 277px;
}
#triangle-box * {
  position: absolute;
}
.btn-inner {
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: inset 0px 6px 1px lightgrey;
}
.btn-outline {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: transparent;
  cursor: pointer;
  box-shadow: inset 0px -6px 1px rgb(0 0 0 / 50%);
}
#paper {
  background: linear-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
}
#scissors {
  background: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
}
#rock {
  background: linear-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
}
/* phase get */
#phase_get {
  display: grid;
  grid-template-areas: "u h" "o o";
}
#userpick {
  grid-area: u;
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-flow: column-reverse;
  padding: 20px;
}
#outcame {
  grid-area: o;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#housepick {
  grid-area: h;
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-flow: column-reverse;
  padding: 20px;
}
.spacer {
  padding: 25px;
}
.btn-inner-get {
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 130px;
  height: 130px;
  box-shadow: inset 0px 6px 1px lightgrey;
}
.btn-outline-get {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: transparent;
  width: 165px;
  height: 165px;
  box-shadow: inset 0px -7px 1px rgb(0 0 0 / 50%);
}
#outcame {
  z-index: 1;
}
#message {
  font-size: 60px;
}
#plyagn {
  background: #fff;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  padding: 10px;
  width: 70%;
}
.winner {
  box-shadow: inset 0px -7px 1px rgb(0 0 0 / 50%),
    0px 0px 0px 40px rgb(31 55 86 / 70%), 0px 0px 5px 41px rgb(20 21 57 / 10%),
    0px 0px 0px 80px rgb(31 55 86 / 60%), 0px 0px 6px 81px rgb(20 21 57 / 10%),
    0px 0px 0px 120px rgb(31 55 86 / 50%), 0px 0px 6px 121px rgb(20 21 57 / 10%);
}
/* footer */
#footer {
  width: 100%;
  display: flex;
  flex-direction: column;
}
#rules {
  color: #fff;
  outline: 2px solid hsl(217, 16%, 45%);
  background: transparent;
  border-radius: 5px;
  border: none;
  text-transform: uppercase;
  padding: 0.5rem 2.5rem;
  justify-self: center;
}
.modal {
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1055;
  display: none;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
}
.dsp-none {
  display: none !important;
}
.dsp-flex {
  display: flex !important;
}
.mdl-container {
  align-items: center;
  width: 100%;
  height: 100vh;
}
.mdl-content {
  display: grid;
  flex-direction: column;
  background: #fff;
  padding: 10px;
  text-align: center;
  height: inherit;
  grid: "a" 1fr "c" 1fr "b" 0.5fr;
}
.close-modal-btn {
  padding: 10px 20px;
  border: none;
  background: none;
  font-size: 1rem;
}

#heading-div {
  grid-area: a;
}
#close-div {
  grid-area: b;
}
#img-div {
  grid-area: c;
}
.mdl-content > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 375px) {
  #header {
    width: 80vw;
  }
  #score-label {
    font-size: 1rem;
  }
  #score {
    font-size: 3rem;
  }
  #rules {
    align-self: center;
  }
  #triangle {
    width: 273.6px;
    height: 249.3px;
  }
  #triangle-box {
    width: 273.6px;
    height: 249.3px;
  }

  .btn-inner {
    width: 100px;
    height: 100px;
  }
  .btn-outline {
    width: 130px;
    height: 130px;
  }

  #paper {
    top: -55px;
    left: -55px;
  }
  #scissors {
    top: -55px;
    right: -55px;
  }
  #rock {
    bottom: -45px;
    left: 70px;
  }
}
@media screen and (min-width: 768px) {
  #header {
    width: 60vw;
  }
  #header img {
    width: initial;
  }
  #score-label {
    font-size: 1.5rem;
  }
  #score {
    font-size: 4rem;
  }
  #rules {
    align-self: end;
  }
  #phase_set {
    padding: 5rem 10rem;
    width: fit-content;
  }

  .btn-inner {
    width: 110px;
    height: 110px;
  }
  .btn-outline {
    width: 145px;
    height: 145px;
    border: none;
  }

  #paper {
    top: -60px;
    left: -60px;
  }
  #scissors {
    top: -60px;
    right: -60px;
  }
  #rock {
    bottom: -50px;
    left: 70px;
  }

  .mdl-container {
    display: flex;
    height: auto;
    justify-content: center;
  }

  .mdl-content {
    grid: "a b" "c c";
    grid-template-rows: 0.3fr 1fr;
    padding: 20px;
    border-radius: 10px;
  }
  .mdl-content > div {
    display: initial;
  }
  #heading-div {
    text-align: start;
  }
  #close-div {
    text-align: end;
  }
  .modal {
    justify-content: center;
  }

  #phase_get {
    display: grid;
    grid-template-areas: "u o h ";
  }

  .btn-inner-get {
    width: 180px;
    height: 180px;
  }
  .btn-outline-get {
    width: 225px;
    height: 225px;
  }
  #rules {
    justify-self: end;
  }
  #userpick {
    flex-flow: column;
  }
  #housepick {
    flex-flow: column;
  }
}
@media screen and (min-width: 1366px) {
  #header {
    width: 51vw;
  }
  #score-label {
    font-size: 2rem;
  }
  #score {
    font-size: 6rem;
  }

  .btn-inner {
    width: 150px;
    height: 150px;
  }
  .btn-outline {
    width: 185px;
    height: 185px;
  }

  #paper {
    top: -70px;
    left: -70px;
  }
  #scissors {
    top: -70px;
    right: -70px;
  }
  #rock {
    bottom: -60px;
    left: 50px;
  }

  .btn-inner-get {
    width: 200px;
    height: 200px;
  }
  .btn-outline-get {
    width: 255px;
    height: 255px;
  }
}

/* frontem */
.attribution {
  font-size: 11px;
  text-align: center;
  color: #fff;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
